.authButtons {
    display: grid;
    grid-template: 100% / 47px 45px 173px;
    grid-template-areas: 'google twitter button';
    grid-column-gap: 21px;
    margin-bottom: 16px;
}

.googleAuth {
    grid-area: google;
    width: 47px;
    height: 47px;
}

.twitterAuth {
    grid-area: twitter;
    width: 45px;
    height: 45px;
}

.signupSubmit,
.loginSubmit {
    grid-area: button;
    border: 1px solid #98B1C4;
    border-radius: 22px;
    background-color: #98B1C4;
}
.signupPage {
    margin: 0 35px;
}

/* .signupPage hr {
    margin: 0px 41.5px 0px 41.5px;
} */

.signupPage p {
    text-align: center;
    margin: 10px 0;
}

.signupPage p:nth-of-type(2) {
    margin: 15px 0;
}

.signupForm {
    display: grid;
    grid-template: repeat(5, 45px) / 100%;
    grid-template-areas: 'username' 'email' 'passwordOne' 'passwordTwo' 'buttons';
    grid-row-gap: 40px;
}

.username {
    grid-area: username;
}

.email {
    grid-area: email;
}

.passwordOne {
    grid-area: passwordOne;
}

.passwordTwo {
    grid-area: passwordTwo;
}

.authButtons {
    grid-area: buttons;
}

/* .username,
.email,
.passwordOne,
.passwordTwo {
    margin: 0px 35px 0px 35px;
} */

.username,
.email,
.passwordOne,
.passwordTwo {
    border: 1px solid #98B1C4;
    border-radius: 23px;
    padding-left: 16px;
}
.loginPage {
    margin: 0 35px;
}

.loginPage p {
    text-align: center;
    margin: 10px 0;
}

.loginPage p:nth-of-type(2) {
    margin: 15px 0;
}

.loginForm {
    display: grid;
    grid-template: repeat(3, 45px) / 100%;
    grid-template-areas: 'email' 'password' 'buttons';
    grid-row-gap: 40px;
}

.email {
    grid-area: email;
}

.password {
    grid-area: password;
}

.authButtons {
    grid-area: buttons;
}

.email,
.password {
    border: 1px solid #98B1C4;
    border-radius: 23px;
    padding-left: 16px;
}
.header {
    display: grid;
    grid-template: 100% / 5em 8.5em 9em;
    grid-template-areas: 'logo search button';
    z-index: 2;
}

.logo {
    grid-area: logo;
    width: 65px;
    height: 65px;
    position: relative;
    left: 8px;
    top: 8px;
}

.searchIconAuth,
.searchIconNonAuth {
    grid-area: search;
    width: 70px;
    height: 70px;
    margin-top: 10px;
}

.searchIconNonAuth {
    visibility: hidden;
}

.signupButton,
.loginButton {
    width: 6.25em;
    height: 2.625em;
    padding-top: 0.65em;
    padding-bottom: 0.85em;
    position: relative;
    top: 1.1875em;
    left: 1.375em;
    box-sizing: border-box;
}

.getstartedButton {
    width: 8em;
    height: 2.625em;
    padding-top: 0.65em;
    padding-bottom: 0.85em;
    /* position: relative;
    top: 1.1875em;
    left: 1.375em; */
    margin-top: 1.1875em;
    box-sizing: border-box;
}

.buttonText {
    color: #FF5733;
    font-size: 1em;
    width: 6em;
    text-align: center;
    margin: auto;
    text-decoration: none;
}

.signupButton {
    grid-area: button;
    border: 1px solid #FF5733;
    border-radius: 20px;
}

.signupButton:hover {
    background-color: #FF5733;
}

.signupButton:hover .buttonText {
    color: white;
}

.loginButton {
    grid-area: button;
    /* margin: 15px 8px 15px 100px; */
    border: 1px solid #98B1C4;
    border-radius: 20px;
    /* color: #414141; */
}

.loginButton:hover {
    background-color: #98B1C4;
}

.loginButton:hover .buttonText {
    color: white;
}

.getstartedButton {
    grid-area: button;
    border: 1px solid #FF5733;
    border-radius: 20px;
}

.getstartedButton:hover {
    background-color: #FF5733;
}

.getstartedButton:hover .buttonText {
    color: white;
}

