.newStory {
    width: 42px;
    height: 42px;
    border: 1px solid #FF7153;
    border-radius: 100%;
    background-color: #FF7153;
    text-align: center;
    position: fixed;
    bottom: 13px;
    right: 16px;
}
.newStory p {
    -webkit-transform: translateY(15%);
            transform: translateY(15%);
    color: white;
    font-size: 24px;
    margin: auto;
}
* {
    margin: 0;
    padding: 0;
    font-family: 'Quicksand', sans-serif;
    font-size: 16px;
}
